.btn-rounded {
  border-radius: 50px;
}
.btn-custom {
  padding-left: 1rem;
  padding-right: 1rem;
  letter-spacing: 0.2rem;
}
.btn-custom2 {
  padding-left: 1rem;
  padding-right: 1rem;
  font-weight: bold;
}
.btn-red-white {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  background: $red-siroco;
  transition: all 0.3s;
  &:hover {
    color: $red-siroco;
  }
  &:before {
    background: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: $red-siroco;
  }
}

.btn-rect {
  border-radius: 0;
}

.btn-red-outline {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $red-siroco;
  background: $white;
  border: 1px solid $red-siroco;
  transition: all 0.3s;
  &:hover {
    color: $white;
    border-color: $blue-siroco;
  }
  &:before {
    background: $blue-siroco;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
  }
  &.active {
    background-color: $blue-siroco;
    color: $white;
    border-color: $blue-siroco;
  }
}

.btn-blue-celeste {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  background: $blue-claro;
  border: 1px solid $blue-claro;
  transition: all 0.3s;
  &:hover {
    color: $white;
    border-color: $blue-siroco;
  }
  &:before {
    background: $blue-siroco;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
  }
  &.active {
    color: $white;
    background-color: $blue-siroco;
    border-color: $blue-siroco;
  }
}

.btn-center-out {
  transform: perspective(1px) translateZ(0);
  overflow: hidden;
}
.btn-center-out:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(0);
  transition-property: transform;
  transition-duration: 0.3s;
}
.btn-center-out:hover:before,
.btn-center-out:focus:before,
.btn-center-out:active:before {
  transform: scale(1.15);
}
.btn-blue-white {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  background: $blue-claro;
}
