.btn-rounded {
  border-radius: 50px; }

.btn-custom {
  padding-left: 1rem;
  padding-right: 1rem;
  letter-spacing: 0.2rem; }

.btn-custom2 {
  padding-left: 1rem;
  padding-right: 1rem;
  font-weight: bold; }

.btn-red-white {
  padding-left: 2rem;
  padding-right: 2rem;
  color: #ffffff;
  background: #F14F78;
  transition: all 0.3s; }
  .btn-red-white:hover {
    color: #F14F78; }
  .btn-red-white:before {
    background: #ffffff;
    border-radius: 50px; }
  .btn-red-white:focus {
    box-shadow: none;
    color: #F14F78; }

.btn-rect {
  border-radius: 0; }

.btn-red-outline {
  padding-left: 2rem;
  padding-right: 2rem;
  color: #F14F78;
  background: #ffffff;
  border: 1px solid #F14F78;
  transition: all 0.3s; }
  .btn-red-outline:hover {
    color: #ffffff;
    border-color: #2f4b91; }
  .btn-red-outline:before {
    background: #2f4b91;
    border-radius: 50px; }
  .btn-red-outline:focus {
    box-shadow: none; }
  .btn-red-outline.active {
    background-color: #2f4b91;
    color: #ffffff;
    border-color: #2f4b91; }

.btn-blue-celeste {
  padding-left: 2rem;
  padding-right: 2rem;
  color: #ffffff;
  background: #3B90DB;
  border: 1px solid #3B90DB;
  transition: all 0.3s; }
  .btn-blue-celeste:hover {
    color: #ffffff;
    border-color: #2f4b91; }
  .btn-blue-celeste:before {
    background: #2f4b91;
    border-radius: 50px; }
  .btn-blue-celeste:focus {
    box-shadow: none; }
  .btn-blue-celeste.active {
    color: #ffffff;
    background-color: #2f4b91;
    border-color: #2f4b91; }

.btn-center-out {
  transform: perspective(1px) translateZ(0);
  overflow: hidden; }

.btn-center-out:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(0);
  transition-property: transform;
  transition-duration: 0.3s; }

.btn-center-out:hover:before,
.btn-center-out:focus:before,
.btn-center-out:active:before {
  transform: scale(1.15); }

.btn-blue-white {
  padding-left: 2rem;
  padding-right: 2rem;
  color: #ffffff;
  background: #3B90DB; }

.accordion-button {
  padding: .6rem 1.25rem;
  font-family: "Roboto";
  font-weight: 300;
  color: #808080; }

.accordion-button::after {
  background-image: url("../images/icons/plus-accordion.png"); }

.accordion-button:not(.collapsed)::after {
  background-image: url("../images/icons/menos.svg"); }

.accordion-button:focus {
  z-index: 3;
  border-color: none;
  outline: 0;
  box-shadow: none; }

.section-contacto {
  background-image: url("../images/olas.png");
  background-repeat: no-repeat;
  background-position: bottom; }
  .section-contacto .form-contacto {
    background-color: #2f4b91;
    color: #ffffff;
    position: relative;
    border-radius: 15px; }
    .section-contacto .form-contacto .envio-success {
      position: absolute;
      border-radius: 15px;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #2f4b91; }
  .section-contacto p.text-form {
    font-family: Roboto; }
    .section-contacto p.text-form span {
      font-weight: 800; }
  .section-contacto span.flag-icon {
    color: #000000; }
  .section-contacto .bootstrap-select > .dropdown-toggle.bs-placeholder,
  .section-contacto .bootstrap-select > .dropdown-toggle.bs-placeholder:hover,
  .section-contacto .bootstrap-select > .dropdown-toggle.bs-placeholder:focus {
    color: #000000; }
  .section-contacto .bootstrap-select .dropdown-toggle:focus,
  .section-contacto .bootstrap-select > select.mobile-device:focus + .dropdown-toggle {
    outline: none !important;
    color: #000000;
    background-color: #fff;
    border-color: #86b7fe !important;
    box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25) !important; }
  .section-contacto .bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
    width: 175px !important; }
    @media (max-width: 767.98px) {
      .section-contacto .bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
        width: 125px !important;
        margin-left: 1rem; } }
  .section-contacto .dropdown-item.active, .section-contacto .dropdown-item:active {
    color: #fff;
    text-decoration: none;
    background-color: #2f4b91; }
  .section-contacto .dropdown-item:focus, .section-contacto .dropdown-item:hover {
    color: #fff;
    background-color: #808080; }
  .section-contacto .dropdown-menu.show {
    z-index: 1050; }
